frames error{0%{-webkit-transform:translate(-5%);transform:translate(-5%)}25%{-webkit-transform:translate(5%);transform:translate(5%)}50%{-webkit-transform:translate(-5%);transform:translate(-5%)}75%{-webkit-transform:translate(5%);transform:translate(5%)}100%{-webkit-transform:translate(0%);transform:translate(0%)}}#root #formContainer form.error>input:first-of-type{background:#f66;transition:background .5s}#root #formContainer form input{place-self:center;border-radius:0 1em 1em 0;border-radius:var(--, 0 1em 1em 0);cursor:pointer;z-index:1;min-height:35px;box-shadow:0px 8px 1.5em -20px #2b2b2b}#root #formContainer form input:first-of-type:focus{cursor:text}#root #formContainer form input:focus{outline:none}#root #formContainer form input[type=text]{--: 1em 0 0 1em;height:calc(8vh);width:calc(35vw - 8vh);border:none;padding:0 3vmin;font-size:1em;font-family:"Rubik"}@media(max-width: 768px){#root #formContainer form input[type=text]{width:calc(100% - 6vmin)}}#root #formContainer form input[type=text]::-webkit-input-placeholder{color:rgba(0,0,0,.5)}#root #formContainer form input[type=text]:-ms-input-placeholder{color:rgba(0,0,0,.5)}#root #formContainer form input[type=text]::placeholder{color:rgba(0,0,0,.5)}#root #formContainer form input[type=submit]{color:transparent;height:8vh;width:8vh;border:none;background:#2b2b2b;background-image:url(/ip-address-tracker/static/media/icon-arrow.2eaaa5ee.svg);background-position:50%;background-size:25%;background-repeat:no-repeat}#root #results{background:#f2f2f2;width:100%;height:100%;display:grid;grid-template-rows:0%;position:relative}#root #results #queryContainer{position:-webkit-sticky;position:sticky;z-index:500;display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-align-content:flex-start;align-content:flex-start;-webkit-align-items:center;align-items:center;-webkit-flex-wrap:nowrap;flex-wrap:nowrap;grid-template-columns:repeat(4, 25%);place-self:start center;height:22vh;width:75vw;background:#fff;-webkit-transform:translate(0, -10vmin);transform:translate(0, -10vmin);border-radius:1em;box-shadow:0 8px 1.5em -15px #2b2b2b}@media(max-width: 768px){#root #results #queryContainer{grid-template-columns:auto;height:35vmax;width:85%;-webkit-transform:translate(0, -18vmax);transform:translate(0, -18vmax);-webkit-flex-direction:column;flex-direction:column}}#root #results #queryContainer div{display:grid;grid-template-rows:40% 60%;width:100%;height:50%}@media(max-width: 768px){#root #results #queryContainer div{border-left:0;grid-gap:8px}}#root #results #queryContainer div:nth-child(2){--:0px}#root #results #queryContainer div:not(.enclosing){border-left:1px solid #d3d3d3;border-right:1px solid #d3d3d3;border-right:var(--, 1px) solid #d3d3d3}#root #results #queryContainer div h4,#root #results #queryContainer div h6{width:calc(100% - 64px);padding:0 32px;margin:0;place-self:center;color:#2b2b2b}@media(max-width: 768px){#root #results #queryContainer div h4,#root #results #queryContainer div h6{-webkit-justify-content:center !important;justify-content:center !important;-webkit-transform:none !important;transform:none !important;padding:0 !important;text-align:center;width:100%}}#root #results #queryContainer div h4{height:120%;display:-webkit-flex;display:flex;-webkit-justify-content:flex-start;justify-content:flex-start;font-size:1.78em;font-weight:500;-webkit-transform:translate(0, -2vh);transform:translate(0, -2vh)}@media(max-width: 768px){#root #results #queryContainer div h4{font-size:1.45em !important}}@media(max-height: 768px){#root #results #queryContainer div h4{font-size:3.5vmin}}#root #results #queryContainer div h4 span.loading{width:70%;height:100%;place-self:start;background:rgba(0,0,0,.1);display:block;position:relative;border-radius:.2em;--: 10%}@media(max-width: 768px){#root #results #queryContainer div h4 span.loading{place-self:center}}#root #results #queryContainer div h4 span.loading::before,#root #results #queryContainer div h4 span.loading::after{position:absolute;content:"";height:5px;background:rgba(0,0,0,.1);left:8px;border-radius:3em;-webkit-animation:loading 1.25s ease infinite alternate;animation:loading 1.25s ease infinite alternate}@-webkit-keyframes loading{to{-webkit-transform:scaleX(1.5) translate(30%);transform:scaleX(1.5) translate(30%)}}@keyframes loading{to{-webkit-transform:scaleX(1.5) translate(30%);transform:scaleX(1.5) translate(30%)}}#root #results #queryContainer div h4 span.loading::before{width:25%;top:calc(24%)}#root #results #queryContainer div h4 span.loading::after{width:50%;top:calc(59%)}#root #results #queryContainer div h6{color:#969696;font-size:.75em;place-self:end start;-webkit-transform:translate(0, -4vh);transform:translate(0, -4vh)}#root #results #map{width:100%;height:100%}#root #results #map .leaflet-top{top:50%}
